import {Operator} from '../types';

export default function Select(props: {id: string, name: string,  options: Operator[], defaultValue?: string}) {
    const {id, name, options, defaultValue} = props;

    const renderOption = (operator: Operator) => (
        <option value={operator.value}>{operator.title}</option>
    );

    return (
        <select id={id} name={name} className="form-control" value={defaultValue}>
            {options.map(renderOption)}
        </select>
    );
}
