import {JSX} from 'preact';
import {$} from '@zui/core';
import {definePlugin} from '@zui/dtable/src/helpers/shared-plugins';
import {DTablePlugin} from '@zui/dtable/src/types/plugin';
import {actions} from '@zui/dtable/src/plugins/actions';
import {toolbar, DTableToolbarTypes} from '@zui/dtable/src/plugins/toolbar';
import {pager} from '@zui/dtable/src/plugins/pager';
import {renderLink, ColLinkSetting} from '@zui/dtable/src/plugins/rich';
import {DTableSortTypeTypes} from '@zui/dtable/src/plugins/sort-type';
import {classes} from '@zui/core';
import {Burn} from '@zentao/burn/src/component/burn';
import {PriLabel} from '@zentao/labels/src/components/pri-label';
import {SeverityLabel} from '@zentao/labels/src/components/severity-label';
import type {BurnProps} from '@zentao/burn/src/types';
import type {RowInfo, DTableWithPlugin} from '@zui/dtable/src/types';
import './style.css';

export interface DTableZentaoTypes {
    options: {
        footToolbar: DTableToolbarTypes['options']['footToolbar'];
        sortLink: DTableSortTypeTypes['options']['sortLink'];
        userMap?: Record<string, string | {realname: string}> | ((user: unknown, row: RowInfo) => string);
        customCols?: false | Partial<{
            custom: {
                text: string,
                url: string,
            };
            setGlobal: {
                text: string;
                url: string;
            };
            reset: {
                text: string;
                url: string;
            };
            resetGlobal: {
                text: string;
                url: string;
            }
        }>;
    },
    col: {
        statusMap?: Record<string, string>,
        statusClassPrefix?: string,
        userMap?: Record<string, string | {realname: string}> | ((user: unknown, row: RowInfo) => string);
        currentUser?: string;
        assignLink?: ColLinkSetting;
        unassignedText?: string;
        iconRender?: (value: unknown, row: RowInfo) => string | JSX.HTMLAttributes<HTMLElement> & Record<string, unknown> | undefined,
        burn?: Partial<BurnProps>,
    },
}

export type DTableZentao = DTableWithPlugin<DTableZentaoTypes, [DTableToolbarTypes]>;

function getUserFromMap(user: unknown, row: RowInfo, userMap?: Record<string, string | {realname: string}> | ((user: unknown, row: RowInfo) => string)): string | undefined {
    if (!userMap) {
        return;
    }
    if (typeof userMap === 'object') {
        const userInfo = userMap[user as string];
        if (typeof userInfo === 'string') {
            return userInfo;
        }
        if (typeof userInfo === 'object' && userInfo) {
            return userInfo.realname;
        }
        return '';
    }
    if (typeof userMap === 'function') {
        return userMap(user, row);
    }
}

export const zentaoPlugin: DTablePlugin<DTableZentaoTypes, [DTableToolbarTypes]> = {
    name: 'zentao',
    plugins: ['group', 'checkable', 'nested', actions, toolbar, pager],
    defaultOptions: {
        colHover: false,
        rowHeight: 36,
        striped: false,
        responsive: true,
        checkable: 'auto',
        nested: 'auto',
        width: '100%',
        footer: undefined,
        showToolbarOnChecked: true,
        checkOnClickRow: true,
        groupDivider: true,
        height(actualHeight) {
            return Math.min(actualHeight, window.innerHeight - 1 - ($('#header').outerHeight() || 0) - ($('#mainMenu').outerHeight() || 0) - ($('#mainNavbar').outerHeight() || 0));
        },
    },
    options(options) {
        const {checkable, footToolbar, footPager, footer, sortLink} = options;
        if (footer === undefined) {
            const items: string[] = [];
            if (checkable) {
                items.push('checkbox');
            }
            if (footToolbar) {
                items.push('toolbar');
                footToolbar.btnProps = Object.assign({
                    type: 'primary',
                    size: 'sm',
                }, footToolbar.btnProps);
            }
            if (checkable) {
                items.push('checkedInfo');
            }
            if (footPager) {
                items.push('flex', 'pager');
            }
            if (items.length) {
                options.footer = items;
            }
        }
        if (typeof sortLink === 'string') {
            options.sortLink = {url: sortLink, 'data-load': 'table'};
        }
        return options;
    },
    i18n: {
        zh_cn: {
            unassigned: '',
        },
        zh_tw: {
            unassigned: '',
        },
        en: {
            unassigned: 'Unassigned',
        },
    },
    colTypes: {
        id: {
            width:    60,       // 
            fixed:    'left',   // 
            align:    'center', // 
            sortType: true,     // 
        },
        checkID: {
            width:    80,      // 
            fixed:    'left',  // 
            align:    'left',  // 
            sortType: true,    // 
            checkbox: true,    //  Checkbox
        },
        title: {
            width:    0.44,    //  44%
            fixed:    'left',  // 
            align:    'left',  // 
            sortType: true,    // 
            flex:     1,       // 
        },
        shortTitle: {
            width:    0.2,     //  20%
            align:    'left',  // 
            sortType: true,    // 
            fixed:    'left',  // 
            checkbox: true,    //  Checkbox
            flex:     1,       // 
        },
        nestedTitle: {
            width:        0.5,     //  50%
            align:        'left',  // 
            sortType:     true,    // 
            fixed:        'left',  // 
            flex:         1,       // 
            nestedToggle: true,    // 
            nestedIndent: 20,      //  20px
        },
        shortNestedTitle: {
            width:        0.33,    //  33%
            align:        'left',  // 
            sortType:     true,    // 
            fixed:        'left',  // 
            flex:         1,       // 
            nestedToggle: true,    // 
            nestedIndent: 20,      //  20px
        },
        status: {
            width: 80,
            align: 'center',
            sortType: true,
            onRenderCell(result, {col, row}) {
                const status = row.data?.[col.name];
                let statusName: string | undefined;
                let statusLabel: string | undefined;
                if (typeof status === 'string') {
                    statusName = status;
                    statusLabel = col.setting.statusMap?.[status];
                } else if (typeof status === 'object' && status) {
                    ({name: statusName, label: statusLabel} = status as {name: string, label?: string});
                }
                result[0] = (<span class={`${col.setting.statusClassPrefix ?? 'status-'}${statusName}`}>{statusLabel ?? statusName}</span>);
                return result;
            },
        },
        user: {
            width:    80,        // 
            align:    'center',  // 
            sortType: true,      // 
            onRenderCell(result, {col, row, value}) {
                const {userMap = this.options.userMap} = col.setting;
                const user = getUserFromMap(value, row, userMap);
                if (user !== undefined) {
                    result[0] = user;
                }
                return result;
            },
        },
        assign: {
            width:    108,       // 
            align:    'left',    // 
            sortType: true,      // 
            cellClass: 'px-1.5', // 
            onRenderCell(result, info) {
                const {col, row, value} = info;
                const {userMap = this.options.userMap, currentUser, assignLink, unassignedText = this.i18n('unassigned')} = col.setting as DTableZentaoTypes['col'];
                const isUnassigned = !value;
                const text = isUnassigned ? unassignedText : (getUserFromMap(value, row, userMap) ?? value);

                result[0] = renderLink(assignLink, info, [
                    <i className="icon icon-hand-right"></i>,
                    <span>{text}</span>,
                ], {
                    'data-toggle': 'modal',
                    className: `dtable-assign-btn${currentUser === value ? ' is-me' : ''}${isUnassigned ? ' is-unassigned' : ''}`,
                });

                return result;
            },
        },
        avatar: {
            width:    44,        // 
            align:    'center',  // 
            sortType: true,      // 
        },
        avatarName: {
            width:    108,       // 
            align:    'left',    // 
            sortType: true,      // 
        },
        avatarBtn: {
            width:    108,       // 
            align:    'left',    // 
            sortType: true,      // 
        },
        category: {
            width:    80,       // 
            align:    'center', // 
            flex:     1,        // 
        },
        desc: {
            width:    160,     // 
            align:    'left',  // 
            flex:     1,       // 
        },
        text: {
            width:    136,     // 
            align:    'left',  // 
            flex:     1,       // 
        },
        icon: {
            width:     52,        // 
            align:     'center',  // 
            cellClass: 'px-1',
            onRenderCell(result, {row, col, value: icon}) {
                const {iconRender} = col.setting;
                let iconProps: JSX.HTMLAttributes<HTMLElement> = {};
                if (typeof iconRender === 'function') {
                    const iconInfo = iconRender(icon, row);
                    if (typeof iconInfo === 'string') {
                        icon = iconInfo;
                    } else if (typeof iconInfo === 'object' && iconInfo) {
                        ({icon, ...iconProps} = iconInfo);
                    }
                }
                if (typeof icon === 'string') {
                    iconProps.className = classes(icon, iconProps.className as string);
                } else if (typeof icon === 'object' && icon) {
                    Object.assign(iconProps, icon);
                }
                result[0] = (<i {...iconProps}></i>);
                return result;
            },
        },
        pri: {
            width:     68,        // 
            align:     'center',  // 
            cellClass: 'px-1',    // 
            sortType:  true,      // 
            onRenderCell(result, {value: pri}) {
                result[0] = <PriLabel pri={pri as string} />;
                return result;
            },
        },
        severity: {
            width:     92,        // 
            align:     'center',  // 
            cellClass: 'px-1',    // 
            sortType:  true,      // 
            onRenderCell(result) {
                const severity = result[0] as string;
                result[0] = <SeverityLabel severity={severity} />;
                return result;
            },
        },
        burn: {
            width:    88,        // 
            align:    'center',  // 
            onRenderCell(result, {col}) {
                const data = result[0] as (string | (number | null)[]);
                if (!data) {
                    return result;
                }
                const {burn} = col.setting;
                const props = {
                    data,
                    className: 'border-b',
                    width: 64,
                    height: 24,
                    responsive: false,
                    ...burn,
                };
                result[0] = <Burn {...props} />;
                return result;
            },
        },
        date: {
            width:    96,        // 
            align:    'center',  // 
            sortType: true,      // 
        },
        time: {
            width:      64,        // 
            align:      'center',  // 
            sortType:   true,      // 
        },
        datetime: {
            width:    128,       // 
            align:    'center',  // 
            sortType: true,      // 
        },
        progress: {
            width:    64,        // 
            align:    'center',  // 
            sortType: true,      // 
        },
        money: {
            width:    96,        // 
            align:    'right',   // 
            sortType: true,      // 
        },
        count: {
            width:    92,        // 
            align:    'right',   // 
            sortType: true,      // 
        },
        number: {
            width:    64,        // 
            align:    'center',  // 
            sortType: true,      // 
        },
        percent: {
            width:    64,        // 
            align:    'center',  // 
            sortType: true,      // 
            format(value: unknown) {
                if (typeof value === 'string') {
                    if (value.endsWith('%')) {
                        return value;
                    }
                }
                return `${value}%`;
            },
        },
        actions: {
            minWidth: 120,       // 
            align:    'left',    // 
            fixed:    'right',   // 
        },
    },
    onRenderCell(result, {row, col, value}) {
        const {iconRender} = col.setting;
        if (typeof iconRender === 'function' && col.type !== 'icon') {
            const icon = iconRender(value, row);
            if (icon) {
                result.unshift(typeof icon === 'object' ? <i {...icon}></i> : <i className={icon}></i>);
            }
        }
        return result;
    },
    onRender() {
        const {customCols} = this.options;
        if (!customCols) {
            return;
        }
        const {custom, setGlobal, reset, resetGlobal} = customCols;
        return {
            children: (
                <div className="absolute gap-3 m-1.5 top-0 right-0 z-20 row">
                    <div className="w-px border-l my-1"></div>
                    <a
                        className="btn ghost square size-sm rounded"
                        data-toggle="dropdown"
                        data-placement="bottom-end"
                    >
                        <i class="icon icon-cog-outline"></i>
                    </a>
                    <menu class="dropdown-menu menu">
                        {custom && <li class="menu-item"><a href={custom.url} data-toggle="modal">{custom.text}</a></li>}
                        {setGlobal && <li class="menu-item"><a href={setGlobal.url}>{setGlobal.text}</a></li>}
                        {reset && <li class="menu-item"><a href={reset.url}>{reset.text}</a></li>}
                        {resetGlobal && <li class="menu-item"><a href={resetGlobal.url}>{resetGlobal.text}</a></li>}
                    </menu>
                </div>
            ),
        };
    },
};

export const zentao = definePlugin(zentaoPlugin, {buildIn: true});
