import {Component} from 'preact';
import {classes} from '@zui/core';
import {store} from '@zui/store';
import {ZinbarState, ZinbarProps, ZinPerf, ZinError} from '../types';
import {ZinbarErrorsBtnList} from './zinbar-errors-btn-list';
import {ZinbarPerfBtn} from './zinbar-perf-btn';
import {ZinbarErrorsPanel} from './zinbar-errors-panel';
import '@zui/css-icons/src/icons/caret.css';
import '../style/index.css';

export class Zinbar extends Component<ZinbarProps, ZinbarState> {
    constructor(props: ZinbarProps) {
        super(props);
        this.state = {
            showPanel: props.defaultShow ?? true,
            showZinbar: !!(props.defaultData?.errors?.length || !store.get('Zinbar:hidden')),
            ...props.defaultData,
        };
        this.togglePanel = this.togglePanel.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
    }

    componentDidMount() {
        document.addEventListener('click', this.handleClickOutside);
    }

    componentWillUnmount() {
        document.removeEventListener('click', this.handleClickOutside);
    }

    handleClickOutside(event: Event) {
        const target = event.target as HTMLElement;
        if (this.state.showPanel && !target.closest('.zinbar')) {
            this.setState({showPanel: false});
        }
    }

    update(perf?: ZinPerf, errors?: ZinError[], basePath?: string) {
        this.setState((prevState) => {
            const isPage = !perf?.id || perf.id === 'page';
            const newState: Partial<ZinbarState> = {};
            if (isPage) {
                if (perf) {
                    newState.pagePerf = {...prevState.pagePerf, ...perf};
                }
                if (errors) {
                    newState.errors = errors;
                }
            } else {
                if (perf) {
                    newState.partPerf = {...prevState.partPerf, ...perf};
                }
                if (errors) {
                    newState.errors = [...(prevState.errors ?? []), ...(errors ?? [])];
                }
            }
            if (basePath) {
                newState.basePath = basePath;
            }
            return newState;
        });
    }

    togglePanel() {
        this.setState({showPanel: !this.state.showPanel});
    }

    toggleZinbar() {
        const show = !this.state.showZinbar;
        this.setState({showZinbar: show}, () => {
            store.set('Zinbar:hidden', !show);
        });
    }

    render() {
        const {errors, pagePerf, partPerf, showZinbar, basePath} = this.state;
        const {fixed} = this.props;
        const hasErrors = errors?.length;
        return (
            <div
                className={classes(
                    'zinbar row h-5 items-stretch gap-px inverse bg-opacity-50',
                    fixed ? 'relative' : 'fixed right-0 bottom-0',
                    {collapse: !showZinbar},
                )}
                style={{zIndex: 9999}}
            >
                <button
                    type="button"
                    data-hint={showZinbar ? 'Collapse' : 'Expand'}
                    className={`w-4 ${(hasErrors && !showZinbar) ? 'danger' : 'bg-dark'} flex items-center justify-center`}
                    style={{marginLeft: -15}}
                    onClick={this.toggleZinbar.bind(this)}
                ><span class={showZinbar ? 'caret-right' : 'caret-left'}></span></button>
                {hasErrors ? <ZinbarErrorsBtnList errors={errors}  onClick={this.togglePanel} /> : null}
                {pagePerf ? <ZinbarPerfBtn perf={pagePerf} /> : null}
                {partPerf ? <ZinbarPerfBtn perf={partPerf} /> : null}
                <ZinbarErrorsPanel show={this.state.showPanel} basePath={basePath} errors={errors} />
            </div>
        );
    }
}
