import {Component, createRef} from 'preact';
import {$} from '@zui/core';
import {BurnProps} from '../types';

export type BurnState = {
    width: number;
    height: number;
};

function formatData(data: BurnProps['data']): (number | null)[] {
    if (typeof data === 'string') {
        return data.split(',').map(value => {
            const num = parseFloat(value);
            return Number.isNaN(num) ? null : num;
        });
    }
    return data;
}

export class Burn extends Component<BurnProps> {
    static defaultProps? = {
        responsive: true,
        lineSize: 1,
        scaleLine: false,
        scaleLineSize: 1,
        bezier: true,
    };

    #ref = createRef<HTMLCanvasElement>();

    #timer = 0;

    #rob?: ResizeObserver;

    #iob?: IntersectionObserver;

    #needDraw = false;

    componentDidMount(): void {
        this.tryDraw = this.tryDraw.bind(this);
        this.tryDraw();

        const container = this.#ref.current?.parentElement;
        if (this.props.responsive !== false) {
            if (container) {
                if (typeof ResizeObserver !== 'undefined') {
                    const rob = new ResizeObserver(this.tryDraw);
                    rob.observe(container);
                    this.#rob = rob;
                }
            }
            if (!this.#rob) {
                window.addEventListener('resize', this.tryDraw);
            }
        }
        if (container && typeof IntersectionObserver !== 'undefined') {
            const iob = new IntersectionObserver(entries => {
                if (this.#needDraw && entries.some(entry => entry.isIntersecting)) {
                    this.tryDraw();
                }
            });
            iob.observe(container);
            this.#iob = iob;
        }
    }

    componentWillUnmount() {
        this.#rob?.disconnect();
        window.removeEventListener('resize', this.tryDraw);
    }

    tryDraw() {
        if (this.#timer) {
            cancelAnimationFrame(this.#timer);
        }
        this.#timer = requestAnimationFrame(() => {
            this.draw();
            this.#timer = 0;
        });
    }

    draw() {
        const cavans = this.#ref.current;
        if (!cavans) {
            return;
        }
        const container = cavans.parentElement!;
        const {width: userWidth, height: userHeight, responsive = true} = this.props;
        let width = userWidth || container.clientWidth;
        let height = userHeight || container.clientHeight;
        if (userWidth && userHeight && responsive) {
            width = container.clientWidth;
            height = Math.floor((userHeight * width) / userWidth);
        }

        cavans.style.width = `${width}px`;
        cavans.style.height = `${height}px`;
        width = width * (window.devicePixelRatio || 1);
        height = height * (window.devicePixelRatio || 1);
        cavans.width = width;
        cavans.height = height;

        if (!$(container).isVisible() && this.#iob) {
            this.#needDraw = true;
            return;
        }

        const {
            lineSize = 1,
            scaleLine = false,
            scaleLineSize,
            scaleLineGap = 1,
            scaleLineDash,
            referenceLine,
            referenceLineSize,
            referenceLineDash,
            color = '#2c78f1',
            fillColor = ['rgba(46, 127, 255, 0.3)', 'rgba(46, 127, 255, 0.05)'],
            lineDash = [],
            bezier,
        } = this.props;
        const data = formatData(this.props.data);

        const pointGap = Math.floor(width / (data.length - 1));
        const maxValue = Math.max(...(data.filter(x => x !== null) as number[]));
        const linePoints = data.map((value, index) => {
            const empty = typeof value !== 'number';
            return {
                x: index * pointGap,
                y: empty ? height : Math.round((1 - value / maxValue) * (height - lineSize)),
                empty,
            };
        });
        let currentPoint = linePoints[0];

        const context = cavans.getContext('2d')!;
        if (scaleLine) {
            const scaleLineColor = typeof scaleLine === 'string' ? scaleLine : 'rgba(100,100,100,.1)';
            context.strokeStyle = scaleLineColor;
            context.lineWidth = scaleLineSize || lineSize;
            if (scaleLineDash) {
                context.setLineDash(scaleLineDash);
            }
            for (let i = 0; i < linePoints.length; ++i) {
                if (i % scaleLineGap !== 0) {
                    continue;
                }
                const point = linePoints[i];
                context.moveTo(point.x, 0);
                context.lineTo(point.x, height);
            }
            context.stroke();
        }

        if (referenceLine && linePoints.length > 1) {
            const referenceLineColor = typeof referenceLine === 'string' ? referenceLine : 'rgba(100,100,100,.2)';
            const lastPoint = linePoints[linePoints.length - 1];
            context.moveTo(lastPoint.x, lastPoint.y);
            context.strokeStyle = referenceLineColor;
            context.lineWidth = referenceLineSize || lineSize;
            context.lineTo(currentPoint.x, currentPoint.y);
            if (referenceLineDash) {
                context.setLineDash(referenceLineDash);
            }
            context.stroke();
        }

        /* Clear line dash */
        context.setLineDash(lineDash);

        while (linePoints.length && linePoints[linePoints.length - 1].empty) {
            linePoints.pop();
        }

        /* Fill chart */
        if (fillColor) {
            const lastPoint = linePoints[linePoints.length - 1];
            context.beginPath();
            context.moveTo(0, height);
            context.lineTo(currentPoint.x, currentPoint.y);
            if (bezier) {
                const halfPointGap = Math.round(pointGap / 2);
                for (let i = 1; i < linePoints.length; ++i) {
                    const point = linePoints[i];
                    const deltaY = Math.round((point.y - currentPoint.y) / 5);
                    context.bezierCurveTo(currentPoint.x + halfPointGap, currentPoint.y + deltaY, point.x - halfPointGap, point.y - deltaY, point.x, point.y);
                    currentPoint = point;
                }
            } else {
                for (let i = 1; i < linePoints.length; ++i) {
                    currentPoint = linePoints[i];
                    context.lineTo(currentPoint.x, currentPoint.y);
                }
            }
            context.lineTo(lastPoint.x, height);
            if (Array.isArray(fillColor)) {
                /* Create linear gradient */
                const gradient = context.createLinearGradient(0, 0, 0, height);
                for (let i = 0; i < fillColor.length; ++i) {
                    gradient.addColorStop(i / (fillColor.length - 1), fillColor[i]);
                }
                context.fillStyle = gradient;
            } else {
                context.fillStyle = fillColor;
            }
            context.fill();
        }

        /* Draw chart line */
        currentPoint = linePoints[0];
        context.beginPath();
        context.moveTo(currentPoint.x, currentPoint.y);
        if (bezier) {
            const halfPointGap = Math.round(pointGap / 2);
            for (let i = 1; i < linePoints.length; ++i) {
                const point = linePoints[i];
                const deltaY = Math.round((point.y - currentPoint.y) / 5);
                context.bezierCurveTo(currentPoint.x + halfPointGap, currentPoint.y + deltaY, point.x - halfPointGap, point.y - deltaY, point.x, point.y);
                currentPoint = point;
            }
        } else {
            for (let i = 1; i < linePoints.length; ++i) {
                currentPoint = linePoints[i];
                context.lineTo(currentPoint.x, currentPoint.y);
            }
        }
        context.strokeStyle = color;
        context.lineWidth = lineSize;
        context.stroke();
    }

    render() {
        const {style, className, canvasClass} = this.props;
        return (
            <div class="center burn-chart" className={className} style={style}>
                <canvas className={canvasClass} ref={this.#ref} />
            </div>
        );
    }
}
