import {Component, $} from '@zui/core';

export type EChartsOptions = {
    responsive?: boolean;
    theme: string;
    [prop: string]: unknown;
};

interface EChartsLib {
    init(element: HTMLElement, theme: string): EChartsComponent;
}

interface EChartsComponent {
    setOption(options: Record<string, unknown>): void;
    dispose(): void;
    resize(): void;
}

export class ECharts extends Component<EChartsOptions, {}, HTMLElement> {
    static NAME = 'ECharts';

    declare chart?: EChartsComponent;

    init() {
        const {echarts} = window as {echarts?: EChartsLib};
        if (!echarts) {
            console.warn('ZUI: ECharts is not loaded.');
            return;
        }

        const {responsive = true, theme, ...options} = this.options;
        const chart = echarts.init(this.element, theme);
        chart.setOption(options);
        if (responsive) {
            $(window).on(`resize.${this.gid}.ECharts.zt`, chart.resize);
        }
        this.chart = chart;
    }

    destroy() {
        const {echarts} = window as {echarts?: EChartsLib};
        if (!echarts) {
            super.destroy();
            return;
        }

        const {responsive = true} = this.options;
        if (responsive) {
            $(window).off(`resize.${this.gid}.ECharts.zt`);
        }
        this.chart?.dispose();
        super.destroy();
    }
}
