import type {JSX} from 'preact';
import {ZinError} from '../types';
import {getErrorType, getPhpErrType} from '../utils/php-error-helper';
import {printData} from '../utils/console';

function handleErrorClick(error: ZinError) {
    printData('Trace', 'Error', error, error.message);
    navigator.clipboard.writeText(`vim +${error.line} ${error.file}`);
}

export function ZinbarErrorsPanel({errors = [], show, basePath}: {errors?: ZinError[], show?: boolean, basePath?: string} & JSX.HTMLAttributes<HTMLDivElement>) {
    if (!errors.length) {
        show = false;
    }

    const list = errors.map(error => {
        const phpErrType = getPhpErrType(error.level);
        const errType = getErrorType(phpErrType);
        const classType = errType === 'error' ? 'danger' : (errType === 'info' ? 'important' : 'warning');
        return (
            <div class={`zin-error-item state ${classType}-pale text-fore px-2 py-1 ring ring-darker`} onClick={handleErrorClick.bind(null, error)}>
                <div class="zin-error-msg font-bold text-base"><strong class={`text-${classType}`} style="text-transform: uppercase;">{phpErrType}</strong> {error.message}</div>
                <div class="zin-error-info text-sm opacity-60 break-all"><strong>vim +{error.line}</strong> <span className="underline">{basePath ? error.file.substring(basePath.length) : error.file}</span></div>
            </div>
        );
    });

    return (
        <div class={`zin-errors-panel absolute bottom-full left-0 mono shadow-xl ring rounded fade-from-bottom ${show ? 'in' : 'events-none'}`}>
            {list}
        </div>
    );
}
