import {Component, JSX} from 'preact';
import {classes} from '@zui/core';
import {GlobalSearchPanelProps} from '../types';

export class GlobalSearchPanel extends Component<GlobalSearchPanelProps> {
    private renderCommonSearch(className?: string) {
        const {commonSearchText, commonSearchKey, searchValue} = this.props;
        return (
            <li key={commonSearchKey} data-key={commonSearchKey} className={classes('w-full rounded flex items-center my-0.5', className)}>
                <a className="inline-block p-1 ellipsis" style={{color: 'inherit'}}>
                    {commonSearchText} {searchValue}
                </a>
            </li>
        );
    }

    handleClick(e: JSX.TargetedMouseEvent<HTMLUListElement>) {
        const target = (e.target as HTMLElement).closest('li');
        if (!target) {
            return;
        }

        const key = target.dataset.key!;
        const {searchFunc, searchValue} = this.props;
        searchFunc(key, searchValue);
    }

    render() {
        const {searchValue, searchItems, selectedKey} = this.props;
        if (searchValue === '') {
            return null;
        }

        if (!/^\d+$/.test(searchValue)) {
            return (
                <ul className="global-search-panel p-1 rounded shadow bg-white w-full" onClick={this.handleClick.bind(this)}>
                    {this.renderCommonSearch('secondary')}
                </ul>
            );
        }

        return (
            <ul className="global-search-panel flex flex-wrap p-1 rounded shadow bg-white w-full" onClick={this.handleClick.bind(this)}>
                {this.renderCommonSearch()}
                {
                    searchItems.map(({key, text}) => (
                        <li key={key}
                            data-key={key}
                            className={classes({
                                rounded: true,
                                'my-0.5': true,
                                'flex': true,
                                'justify-between': true,
                                'items-center': true,
                                'w-1/2': key !== selectedKey,
                                'w-full': key === selectedKey,
                                secondary: key === selectedKey,
                                'order-first': key === selectedKey,
                            })}
                        >
                            <a className="inline-block p-1 ellipsis" style={{color: 'inherit'}}>
                                {text} #{searchValue}
                            </a>
                            {key === selectedKey && <i className="icon icon-check mr-2" />}
                        </li>
                    ))
                }
            </ul>
        );
    }
}
