/* eslint-disable @typescript-eslint/no-explicit-any */
import {Messager} from '@zui/messager';
import {classes} from '@zui/core';

Object.assign(Messager, {
    success(options: Exclude<Parameters<typeof Messager.show>[0], string>) {
        if (Array.isArray((options.actions as any)?.items) && (options.actions as any).items.length > 0) {
            (options.actions as any).items.forEach((item: any) => {
                item.btnType = classes('text-primary text-lg font-bold ghost', item.btnType);
            });
        }

        return Messager.show(Object.assign({
            icon: 'icon-check',
            iconClass: 'center w-6 h-6 rounded-full m-0 success',
            contentClass: 'text-lg font-bold',
            close: false,
            className: 'p-6 bg-white text-black gap-2 messager-success',
        }, options));
    },
    fail(options: Exclude<Parameters<typeof Messager.show>[0], string>) {
        if (Array.isArray((options.actions as any)?.items) && (options.actions as any).items.length > 0) {
            (options.actions as any).items.forEach((item: any) => {
                item.btnType = classes('text-primary text-lg font-bold ghost', item.btnType);
            });
        }

        return Messager.show(Object.assign({
            icon: 'icon-close',
            iconClass: 'center w-6 h-6 rounded-full m-0 danger',
            contentClass: 'text-lg font-bold',
            close: false,
            className: 'p-6 bg-white text-black gap-2 messager-fail',
        }, options));
    },
});
