import type {JSX} from 'preact';
import {ZinError} from '../types';
import {getErrorType} from '../utils/php-error-helper';

export function ZinbarErrorsBtnList({errors, ...others}: {errors: ZinError[]} & JSX.HTMLAttributes<HTMLDivElement>) {
    const typeCount = errors.reduce((count, error) => {
        count[getErrorType(error.level)]++;
        return count;
    }, {error: 0, warning: 0, info: 0});

    return (
        <div class="row items-stretch text-sm" data-hint="PHP errors" {...others}>
            {typeCount.error ? <button type="button" class="state font-bold px-0.5 danger"><span class="scale-95 font-bold inline-block text-opacity-70 text-canvas">ERR</span>{typeCount.error}</button> : null}
            {typeCount.warning ? <button type="button" class="state font-bold px-0.5 danger bg-opacity-90"><span class="scale-95 font-bold inline-block text-opacity-70 text-canvas">WAR</span>{typeCount.warning}</button> : null}
            {typeCount.info ? <button type="button" class="state font-bold px-0.5 danger bg-opacity-80"><span class="scale-95 font-bold inline-block text-opacity-70 text-canvas">INF</span>{typeCount.info}</button> : null}
        </div>
    );
}
