import {Component, JSX} from 'preact';
import {SearchFormProps, SearchFormState} from '../types';
import SearchBox from './search-box';
import Select from './select';
import {classes} from '@zui/core';
import SearchCondition from './search-condition';

export class SearchForm extends Component<SearchFormProps, SearchFormState> {
    constructor(props: SearchFormProps) {
        super(props);

        const {toggleSide = true, formSession} = props;
        this.state = {
            toggleMore: false,
            toggleSide,
            formSession,
            formKey: Date.now().toString(),
        };
    }

    handleSubmit(e: JSX.TargetedEvent<HTMLFormElement, Event>) {
        e.preventDefault();
        const {formConfig, actionURL, module, onSubmit} = this.props;
        const {action, method} = formConfig;
        const formData = new FormData(e.target as HTMLFormElement);
        formData.append('module', module);
        formData.append('actionURL', actionURL);
        const response = fetch(action, {method, body: formData, headers: {'X-Requested-With': 'XMLHttpRequest'}});
        onSubmit?.(response);
    }

    handleReset() {
        this.setState({
            formSession: [{}, {}, {}, {}, {}, {}, {}],
            formKey: Date.now().toString(),
        });
    }

    render(props: SearchFormProps, state: SearchFormState) {
        const {fields, operators, andOr, groupName, saveSearch, savedQueryTitle, searchConditions, className, submitText, resetText} = props;
        const {toggleMore, toggleSide, formKey, formSession} = state;
        return (
            <form className={classes('flex', 'bg-white', className)} onSubmit={this.handleSubmit.bind(this)}>
                <table className="grow" key={formKey} style={{tableLayout: 'fixed'}}>
                    <tr>
                        <td>
                            <table className="w-full" style={{tableLayout: 'fixed'}}>
                                <SearchBox
                                    show
                                    index={1}
                                    fields={fields}
                                    operators={operators}
                                    andOr={andOr}
                                    groupName={groupName}
                                    defaultValue={formSession[0]}
                                />
                                <SearchBox
                                    show={toggleMore}
                                    index={2}
                                    fields={fields}
                                    operators={operators}
                                    andOr={andOr}
                                    groupName={groupName}
                                    defaultValue={formSession[1]}
                                />
                                <SearchBox
                                    show={toggleMore}
                                    index={3}
                                    fields={fields}
                                    operators={operators}
                                    andOr={andOr}
                                    groupName={groupName}
                                    defaultValue={formSession[2]}
                                />
                            </table>
                        </td>
                        <td className="p-2" style={{width: 140}}>
                            <Select
                                id="groupAndOr"
                                name="groupAndOr"
                                options={props.andOr}
                                defaultValue={formSession[6].groupAndOr}
                            />
                        </td>
                        <td>
                            <table className="w-full" style={{tableLayout: 'fixed'}}>
                                <SearchBox
                                    show
                                    index={4}
                                    fields={fields}
                                    operators={operators}
                                    andOr={andOr}
                                    groupName={groupName}
                                    defaultValue={formSession[3]}
                                />
                                <SearchBox
                                    show={toggleMore}
                                    index={5}
                                    fields={fields}
                                    operators={operators}
                                    andOr={andOr}
                                    groupName={groupName}
                                    defaultValue={formSession[4]}
                                />
                                <SearchBox
                                    show={toggleMore}
                                    index={6}
                                    fields={fields}
                                    operators={operators}
                                    andOr={andOr}
                                    groupName={groupName}
                                    defaultValue={formSession[5]}
                                />
                            </table>
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                        <td className="text-center p-2">
                            <div className="w-full flex justify-center gap-2">
                                <button className="btn primary" type="submit">{submitText}</button>
                                <button className="btn" type="button" onClick={this.handleReset.bind(this)}>{resetText}</button>
                            </div>
                        </td>
                        <td className="text-right">
                            <button
                                className="btn btn-link"
                                disabled={!saveSearch.hasPriv}
                                type="button"
                                data-toggle={saveSearch.config['data-toggle']}
                                data-type={saveSearch.config['data-type']}
                                data-data-type={saveSearch.config['data-data-type']}
                                data-url={saveSearch.config['data-url']}
                            ><i className="icon icon-save" />{saveSearch.text}</button>
                            <button className="btn btn-link" type="button" onClick={() => this.setState({toggleMore: !toggleMore})}>
                                <i className={classes({
                                    icon: true,
                                    'icon-chevron-double-up': toggleMore,
                                    'icon-chevron-double-down': !toggleMore,
                                })} />
                            </button>
                        </td>
                    </tr>
                </table>
                <button
                    type="button"
                    className="secondary self-center rounded-lg"
                    style={{height: 'min-content'}}
                    onClick={() => this.setState({toggleSide: !toggleSide})}
                >
                    <i className={classes({
                        icon: true,
                        'icon-angle-left': toggleSide,
                        'icon-angle-right': !toggleSide,
                    })} />
                </button>
                <div
                    style={{width: 230, height: toggleMore ? 180 : 84}}
                    className={classes({
                        'border-l': true,
                        hidden: toggleSide,
                        col: true,
                        'flex-none': true,
                    })}
                >
                    <strong className="pl-2 py-2">{savedQueryTitle}</strong>
                    <div className="grow overflow-y-auto col gap-2 p-2">
                        {
                            searchConditions.map(x => <SearchCondition {...x} />)
                        }
                    </div>
                </div>
            </form>
        );
    }
}
