import {ComponentChildren} from 'preact';
import {Pick} from '@zui/pick/src/components/pick';
import {PriLabel} from '@zentao/labels/src/components/pri-label';
import {PickOptions, PickState, PickTriggerProps} from '@zui/pick/src/types';
import {PriPickerOptions} from '../types';
import {classes} from '@zui/core/src/helpers';

export class PriPicker<O extends PriPickerOptions = PriPickerOptions> extends Pick<PickState, O> {
    static defaultProps = {
        ...Pick.defaultProps,
        items: ['', 1, 2, 3, 4],
        className: 'pick-pri form-control',
        popClass: 'pick-pri-pop popup',
        popWidth: 'auto',
        popMinWidth: 56,
    } as Partial<PickOptions>;

    constructor(props: O) {
        super(props);
        if (this.state.value === undefined && props.required) {
            const items = this._getItems();
            if (items) {
                (this.state as PickState).value = items[0].value;
            }
        }
    }

    _getTriggerProps(props: O, state: PickState): PickTriggerProps<PickState> {
        const triggerProps = super._getTriggerProps(props, state);
        if (props.disabled) {
            triggerProps.className = classes(triggerProps.className, 'disabled');
        }
        return triggerProps;
    }

    _renderTrigger(props: O, state: PickState): ComponentChildren {
        const {value: currentValue} = state;
        const {placeholder} = props;
        return [
            currentValue === undefined ? <span class="placeholder">{placeholder}</span> : this._renderItem(state.value, this._getItems().find(({value}) => value === currentValue)?.text, 'pri'),
            <span key="caret" class="caret"></span>,
        ];
    }

    _getItems(): {value: string, text?: string}[] {
        let {items = ['', 1, 2, 3, 4]} = this.props;
        if (Array.isArray(items)) {
            items = items.map((value) => {
                if (typeof value === 'object') {
                    return value;
                }
                return {value: `${value}`};
            });
        } else if (typeof items === 'object') {
            items = Object.keys(items).map((value) => ({value, text: (items as Record<string, string>)[value]}));
        }
        items.sort((a, b) => (+a.value - +b.value));
        return items;
    }

    _renderItem(value?: string, text?: string, key?: string) {
        return <PriLabel key={key} pri={value} text={text} />;
    }

    _renderPop(): ComponentChildren {
        const {value: currentValue} = this.state;
        return (
            <div className="pick-pri-list">
                {this._getItems().map(({value, text}) => (<button key={value} type="button" class={`btn w-full ${currentValue === value ? 'primary-pale' : 'ghost'}`} data-pick-value={value}>{this._renderItem(value, text)}</button>))}
            </div>
        );
    }
}
