import {$} from '@zui/core';
import type {Selector} from 'cash-dom';

/* Declare types. */
declare module 'cash-dom' {
    interface Cash {
        autoHeight(this: Cash): Cash;
    }
}

function autoHeight(selector: Selector) {
    const $element = $(selector);
    $element.css({minHeight: 0}).css({minHeight: Math.max(32, ($element[0] as HTMLTextAreaElement).scrollHeight)});
}

$.fn.autoHeight = function () {
    return this.each(function () {
        const $element = $(this);
        if (!$element.data('auto-height')) {
            $element.on('input paste change', function (this: Element) {
                autoHeight(this);
            }).data('auto-height', true);
        }
        autoHeight($element);
    });
};
