import {Component, Cash, $} from '@zui/core';
import {Moveable} from '@zui/dnd';
import {ImgCutterProps} from '../types';
import '../style/index';

export class ImgCutter extends Component<ImgCutterProps> {
    static NAME = 'ImgCutter';

    static DEFAULT = {
        coverColor: '#000',
        coverOpacity: 0.6,
        defaultWidth: 128,
        defaultHeight: 128,
        minWidth: 48,
        minHeight: 48,
        fixedRatio: true,
    };

    private declare $canvas: Cash;

    private declare $img: Cash;

    private declare $btn: Cash;

    private declare $controller: Cash;

    private declare $cliper: Cash;

    private declare $chipImg: Cash;

    // 
    private declare clipWidth: number;

    // 
    private declare clipHeight: number;

    // 
    private declare imgWidth: number;

    // 
    private declare imgHeight: number;

    private declare left: number;

    private declare top: number;

    private declare startLeft: number;

    private declare startTop: number;

    // 
    private declare startClipWidth: number;

    // 
    private declare startClipHeight: number;

    private declare width: number;

    private declare height: number;

    private declare right: number;

    private declare bottom: number;

    private declare moveable: Moveable;

    init() {
        this.clipWidth = this.options.defaultWidth!;
        this.clipHeight = this.options.defaultHeight!;
        this.initDom();
        this.initSize();
        this.bindEvents();
    }

    private initDom() {
        this.$canvas = this.$element.find('.canvas');
        this.$img = this.$canvas.find('img');
        this.$btn = this.$element.find('button');
        const src = this.$img.prop('src');

        const {coverColor, coverOpacity} = this.options;
        this.$canvas.append([
            `<div class="cover" style="background: ${coverColor}; opacity: ${coverOpacity};"></div>`,
            `<div class="controller" style="width: ${this.clipWidth}px; height: ${this.clipHeight}px">`,
            ['top', 'right', 'bottom', 'left', 'top-left', 'top-right', 'bottom-left', 'bottom-right'].map(type => `<div class="control" data-type="${type}"></div>`).join(''),
            '</div>',
            '<div class="cliper">',
            `<img src="${src}"/>`,
            '</div>',
        ].join(''));

        this.$controller = this.$canvas.find('.controller');
        this.$cliper = this.$canvas.find('.cliper');
        this.$chipImg = this.$cliper.find('img');
    }

    public resetImage(src: string) {
        this.$img.prop('src', src);
        this.$chipImg.prop('src', src);
        this.imgWidth = 0;
        this.left = -1;
        this.initSize();
    }

    private initSize() {
        const {minWidth, minHeight, ready, onSizeError} = this.options;
        if (this.imgWidth) {
            return;
        }

        const handleImgLoad = (image: HTMLImageElement) => {
            this.imgWidth = image.width;
            this.imgHeight = image.height;

            if (image.width < minWidth! || image.height < minHeight!) {
                onSizeError?.({width: image.width, height: image.height});
                this.options.minWidth = Math.min(image.width, minWidth!);
                this.options.minHeight = Math.min(image.height, minHeight!);
            }
            ready?.();

            this.width = Math.min(this.imgWidth, this.$element.width());
            this.$canvas.css('width', this.width);
            this.height = this.$canvas.height();

            if (this.left === undefined || this.left < 0) {
                this.left = Math.floor((this.width - this.$controller.width()) / 2);
                this.top = Math.floor((this.height - this.$controller.height()) / 2);
            }

            this.refreshSize();
        };

        const image = this.$img[0] as HTMLImageElement;
        if (image.complete) {
            handleImgLoad(image);
        } else {
            image.onload = () => {
                handleImgLoad(image);
            };
        }
    }

    private refreshSize(ratioSide?: string | boolean) {
        const {minWidth, minHeight, fixedRatio, defaultWidth, defaultHeight} = this.options as Required<typeof this.options>;
        this.clipWidth = Math.max(minWidth, Math.min(this.width, this.clipWidth));
        this.clipHeight = Math.max(minHeight, Math.min(this.height, this.clipHeight));

        if (fixedRatio) {
            if (ratioSide && ratioSide === 'height') {
                this.clipWidth = Math.max(minWidth, Math.min(this.width, this.clipHeight * defaultWidth / defaultHeight));
                this.clipHeight = this.clipWidth * defaultHeight / defaultWidth;
            } else {
                this.clipHeight = Math.max(minHeight, Math.min(this.height, this.clipWidth * defaultHeight / defaultWidth));
                this.clipWidth = this.clipHeight * defaultWidth / defaultHeight;
            }
        }

        this.left = Math.min(this.width - this.clipWidth, Math.max(0, this.left));
        this.top = Math.min(this.height - this.clipHeight, Math.max(0, this.top));
        this.right = this.left + this.clipWidth;
        this.bottom = this.top + this.clipHeight;

        this.$controller.css({
            left: this.left,
            top: this.top,
            width: this.clipWidth,
            height: this.clipHeight,
        });
        this.$cliper.css('clip', `rect(${this.top}px, ${this.left + this.clipWidth}px, ${this.top + this.clipHeight}px, ${this.left}px)`);
    }

    public getData() {
        return {
            originWidth: this.imgWidth,
            originHeight: this.imgHeight,
            scaleWidth: this.width,
            scaleHeight: this.height,
            width: this.right - this.left,
            height: this.bottom - this.top,
            left: this.left,
            top: this.top,
            right: this.right,
            bottom: this.bottom,
            scaled: this.imgWidth !== this.width || this.imgHeight !== this.height,
        };
    }

    private bindEvents() {
        this.$element.on('resize', this.initSize.bind(this));
        this.$btn.on('click', (e) => {
            this.options.handleBtnClick?.(e, this.getData());
        });

        this.moveable = new Moveable(this.$controller, {
            selector: '.control,.controller',
            move: false,
            onMoveStart: () => {
                this.startLeft = this.left;
                this.startTop = this.top;
                this.startClipWidth = this.clipWidth;
                this.startClipHeight = this.clipHeight;
            },
            onMove: (_event, state) => {
                const $target = $(state.target);
                if ($target.is('.controller')) {
                    this.left = this.startLeft + state.deltaX;
                    this.top = this.startTop + state.deltaY;
                    this.refreshSize();
                } else {
                    const dr: string = $target.data('type');
                    let ratioSide = false;
                    const {minWidth, minHeight} = this.options as Required<typeof this.options>;

                    switch (dr) {
                        case 'left':
                        case 'top-left':
                        case 'bottom-left':
                            this.left = Math.min(this.right - minWidth, Math.max(0, this.startLeft + state.deltaX));
                            this.clipWidth = this.right - this.left;
                            break;
                        case 'right':
                        case 'top-right':
                        case 'bottom-right':
                            this.clipWidth = Math.min(this.width - this.left, Math.max(minWidth, this.startClipWidth + state.deltaX));
                            break;
                    }
                    switch (dr) {
                        case 'top':
                        case 'top-left':
                        case 'top-right':
                            this.top = Math.min(this.bottom - minHeight, Math.max(0, this.startTop + state.deltaY));
                            this.clipHeight = this.bottom - this.top;
                            ratioSide = true;
                            break;
                        case 'bottom':
                        case 'bottom-left':
                        case 'bottom-right':
                            this.clipHeight = Math.min(this.height - this.top, Math.max(minHeight, this.startClipHeight + state.deltaY));
                            ratioSide = true;
                            break;
                    }

                    this.refreshSize(ratioSide);
                }
            },
        });
    }

    destroy(): void {
        this.moveable?.destroy();
        super.destroy();
    }
}
