import {DefaultValue, Field, Operator} from '../types';
import {Picker} from '@zui/picker/src/component/picker';
import Select from './select';
import {useState} from 'preact/hooks';

type SearchBoxProps = {
    index: number;
    fields: Field[];
    operators: Operator[];
    andOr: Operator[];
    groupName: [string, string];
    defaultValue: DefaultValue;
    show: boolean;
};

export default function SearchBox(props: SearchBoxProps) {
    const {index, fields, operators, andOr, groupName, defaultValue, show = false} = props;
    const [selectedKey, setSelectedKey] = useState<string | undefined>(defaultValue.field);

    const selectedField = fields.find(x => x.name === selectedKey);

    const getPickerItems = (values: Record<string, string>) => {
        return Array.from(Object.entries(values))
            .map(([value, text]) => ({value, text}));
    };

    if (!show) {
        return null;
    }

    return (
        <tr>
            <td className="p-2 text-right" style={{width: 80}}>
                {
                    index === 1
                        ? <span><strong>{groupName[0]}</strong></span>
                        : index === 4
                            ? <span><strong>{groupName[1]}</strong></span>
                            : <Select
                                id={`andOr${index}`}
                                name={`andOr${index}`}
                                options={andOr}
                                defaultValue={defaultValue.andOr}
                            />
                }
            </td>
            <td className="p-2" style={{width: 150}}>
                <Picker
                    className="w-full"
                    id={`field${index}`}
                    name={`field${index}`}
                    items={fields.map(x => ({text: x.label, value: x.name}))}
                    onChange={(key) => setSelectedKey(key)}
                    defaultValue={defaultValue.field}
                />
            </td>
            <td className="p-2" style={{width: 90}}>
                <Select
                    id={`operator${index}`}
                    name={`operator${index}`}
                    options={operators}
                    defaultValue={defaultValue.operator}
                />
            </td>
            <td className="p-2">
                {
                    selectedField?.control === 'input' || !selectedField
                        ? <input
                            id={`value${index}`}
                            name={`value${index}`}
                            type="text"
                            className="form-control"
                            placeholder={selectedField?.placeholder}
                            defaultValue={defaultValue.value}
                        />
                        : <Picker
                            id={`value${index}`}
                            name={`value${index}`}
                            items={getPickerItems(selectedField?.values as Record<string, string>)}
                            defaultValue={defaultValue.value}
                        />
                }
            </td>
        </tr>
    );
}
