import type {ComponentChildren, JSX} from 'preact';
import {Icon} from '@zui/core';
import {PickTrigger} from '@zui/pick/src/components';
import '@zui/css-icons/src/icons/caret.css';
import type {DropmenuState, DropmenuBtnProps} from '../types';

export class DropmenuBtn extends PickTrigger<DropmenuState, DropmenuBtnProps> {
    protected _getProps(props: DropmenuBtnProps): JSX.HTMLAttributes<HTMLElement> {
        const btnProps = super._getProps(props);
        return {
            type: 'button',
            'data-value': props.state.value,
            ...btnProps,
        } as JSX.HTMLAttributes<HTMLElement>;
    }

    protected _renderTrigger(props: DropmenuBtnProps): ComponentChildren {
        const {text, state, children, caret, leadingAngle} = props;
        return [
            leadingAngle && <Icon icon="angle-right" className="is-leading" />,
            <span key="text" className="text">{state.text ?? text ?? children}</span>,
            caret && <div key="caret" class="is-caret"><span className="caret"></span></div>,
        ];
    }
}
