export function printData(type: string, summary: string, data: unknown, extra?: unknown) {
    console.groupCollapsed(`%c${type} %c${summary}`, 'color: #fff; background-color: #9333ea; padding: 0 0.1em 0 0.25em; border-radius: 0.25em 0 0 0.25em;', 'color: #9333ea; background-color: #e9d5ff; padding: 0 0.5em; border-radius: 0 0.25em 0.25em 0;', extra);
    console.table(data);
    console.groupEnd();
}
