import {$, Cash} from '@zui/core';
import {Modal} from '@zui/modal';
import {Messager} from '@zui/messager';
import {AjaxFormResult, AjaxSubmitOptions} from '../types';

function handleSubmitResult(result: AjaxFormResult, options: AjaxSubmitOptions) {
    const {message} = result;
    const hasMessage = typeof message === 'string' && message.length;
    const isSuccess = result.result === 'success';
    if (isSuccess) {
        if (options.onSuccess?.(result) === false) {
            return;
        }
    } else {
        if (options.onFail?.(result) === false) {
            return;
        }
    }

    if (hasMessage) {
        if (options.onMessage) {
            options.onMessage(message, result);
        } else if (isSuccess) {
            Messager.show({content: message, type: 'success'});
        } else {
            Modal.alert({message});
        }
    }

    const closeModal = result.closeModal || options.closeModal;
    if (closeModal) {
        Modal.hide(typeof closeModal === 'string' ? closeModal : undefined);
    }

    // Handle callback
    const callback = result.callback || options.callback;
    if (callback) {
        const callbackParams: [string, unknown][] = [['options', options], ['result', result]];
        if (typeof callback === 'string') {
            const func = $.runJS(callback, ...callbackParams);
            if (typeof func === 'function' && !callback.endsWith(';')) {
                func(result);
            }
        } else if (typeof callback === 'object') {
            const func = $.runJS(callback.name, ...callbackParams);
            if (typeof func === 'function') {
                // eslint-disable-next-line prefer-spread
                func.apply(null, Array.isArray(callback.params) ? callback.params : [callback.params]);
            }
        }
    }

    // Handle locate
    const load = result.load || options.load || result.locate;
    if (load) {
        $(options.element || document).trigger('locate.zt', load);
    }
}

export async function ajaxSubmit(options: AjaxSubmitOptions): Promise<[result: AjaxFormResult | undefined, error: Error | undefined]> {
    if (options.confirm) {
        const confirmed = await Modal.confirm(options.confirm);
        if (!confirmed) {
            return [undefined, new Error('canceled')];
        }
        return ajaxSubmit({...options, confirm: undefined});
    }

    if (options.beforeSubmit) {
        const result = await options.beforeSubmit(options);
        if (result === false) {
            return [undefined, new Error('canceled')];
        }
    }

    const {loadingClass, element} = options;
    if (element && loadingClass) {
        $(element).addClass(loadingClass);
    }

    const {data} = options;
    let formData: FormData | undefined;
    if (data instanceof FormData) {
        formData = data;
    } else if (data) {
        formData = new FormData();
        for (const [name, value] of Object.entries(data)) {
            if (Array.isArray(value)) {
                for (const item of value) {
                    formData.append(name, item as string);
                }
                continue;
            } else {
                formData.append(name, value as string);
            }
        }
    }

    const {beforeSend} = options;
    if (beforeSend) {
        const result = beforeSend(formData);
        if (result instanceof FormData) {
            formData = result;
        }
    }

    let error: Error | undefined;
    let responseText: string | undefined;
    let result: AjaxFormResult | undefined;
    try {
        const response = await fetch(options.url, {
            method: options.method || 'POST',
            body: formData,
            credentials: 'same-origin',
            headers: {
                'X-Requested-With': 'XMLHttpRequest',
                ...options.headers,
            },
        });

        responseText = await response.text();
        if (response.ok) {
            result = JSON.parse(responseText) as AjaxFormResult;
            if (!result || typeof result !== 'object') {
                error = new Error('Invalid json format');
            }
        } else {
            error = new Error(response.statusText);
        }
    } catch (err) {
        error = err as Error;
    }

    if (error) {
        options.onError?.(error, responseText);
    } else {
        handleSubmitResult(result!, options);
    }

    options.onComplete?.(result, error);

    if (element && loadingClass) {
        $(element).removeClass(loadingClass);
    }

    return [result, error];
}

$.extend($, {ajaxSubmit});

$(document).on('click.ajaxSubmit.zui', '.ajax-submit', function (this: Cash, e: MouseEvent) {
    e.preventDefault();
    const $this = $(this);
    const options = $this.data() as AjaxSubmitOptions;
    if (!options.url && $this.is('a')) {
        options.url = $this.attr('href') || '';
    }
    if (options.url) {
        options.element = this[0];
        ajaxSubmit(options);
    }
});
