import {ComponentChildren} from 'preact';
import {Tree} from '@zui/tree/src/components/tree';
import {TreeItemOptions, TreeItemProps, TreeOptions} from '@zui/tree';
import {underlineKeys} from '@zui/picker/src/component/picker-menu';
import {Icon, IconType, classes} from '@zui/core';
import type {DropmenuTreeProps, DropmenuItemOptions} from '../types';
import {formatString} from '@zui/helpers/src/format-string';

function getTreeItemProps(this: DropmenuTreeProps, treeItem: TreeItemProps): TreeItemProps {
    const {link, itemProps = {}, typeIconMap = {}, labelMap = {}, searchKeys, active, itemType = '', checkIcon, hideDirIcon} = this;
    const {
        id, keys, text, className, url, type = itemType, items, icon, label, ...others
    } = treeItem as DropmenuItemOptions;
    let labelView: ComponentChildren = items ? (label ?? (labelMap[type] || type)) : null;
    let labelText: string | undefined;
    if (typeof labelView === 'string' && labelView.length) {
        labelText = labelView;
        labelView = <span class="label rounded-full lighter size-sm ml-1">{labelView}</span>;
    }
    let itemUrl = url;
    if (itemUrl === undefined && link) {
        if (typeof link === 'function') itemUrl = link(treeItem as DropmenuItemOptions);
        else if (typeof link === 'object') {
            itemUrl = link[type];
            if (itemUrl) {
                itemUrl = formatString(itemUrl, treeItem);
            }
        } else {
            itemUrl = formatString(link, treeItem);
        }
    }
    let iconView = icon;
    if (iconView === undefined && (!hideDirIcon || itemUrl)) {
        iconView = typeIconMap[type];
    }
    if (typeof iconView === 'string') {
        iconView = {icon: iconView, 'data-toggle': 'tooltip', 'data-title': labelText || type} as IconType;
    }
    let itemText: ComponentChildren = text ?? (labelMap[type] || type);
    if (searchKeys?.length) {
        itemText = underlineKeys(searchKeys, [itemText]);
    }
    const isActive = active === id;
    if (checkIcon && isActive) {
        labelView = [labelView, <div className="dropmenu-item-check"><Icon icon="check" /></div>];
    }
    return {
        type: 'item',
        key: id,
        children: labelView,
        icon: iconView,
        items: items as TreeItemProps[],
        'data-url': itemUrl,
        text: itemText,
        active: isActive,
        title: text,
        ...itemProps,
        ...others,
        className: classes('dropmenu-item rounded', className, itemProps.className, items ? 'is-dir' : 'is-item', itemUrl ? 'is-link open-url' : 'is-toggle'),
    } as TreeItemProps;
}

export function DropmenuTree(props: DropmenuTreeProps) {
    const {
        className,
        items,
        tree,
        onClickItem,
    } = props;
    return (
        <Tree
            items={items as TreeItemOptions[]}
            itemRenderProps={getTreeItemProps.bind(props)}
            className={classes(className, 'dropmenu-tree')}
            defaultNestedShow
            onClickItem={onClickItem as TreeOptions['onClickItem']}
            {...tree}
        />
    );
}
