import {classes} from '@zui/core';

export type SeverityLabelProps = {
    severity?: string;
    text?: string;
    className?: string;
};

export function SeverityLabel({severity = '', text, className}: SeverityLabelProps) {
    const severityText = text ?? severity;
    const isLabel = `${Number.parseInt(severityText)}` !== `${severity}`;
    return (
        <span className={classes(`severity${isLabel ? ' severity-label' : ''}`, className)} data-severity={severity}>{isLabel ? severityText : ''}</span>
    );
}
