import {ComponentChildren, createRef, RefObject, Fragment} from 'preact';
import {JSX, $, classes} from '@zui/core';
import {PickPop} from '@zui/pick/src/components';
import {Button} from '@zui/button/src/component/button';
import {SearchBox} from '@zui/search-box/src/components';
import {DropmenuTree} from './dropmenu-tree';
import type {DropmenuState, DropmenuPopProps, DropmenuPopState, DropmenuFetchOptions, DropmenuItemOptions, DropmenuData, DropmenuTreeProps} from '../types';
import '@zui/css-icons/src/icons/close.css';

type DropmenuTabData = {
    name: string;
    items: DropmenuItemOptions[];
    text?: string;
    count: number;
};

const isItemMatch = (item: DropmenuItemOptions, searchKeys: string[]) => {
    const {keys = '', text} = item;
    return !searchKeys.length || searchKeys.every(searchKey => keys.toLowerCase().includes(searchKey) || (typeof text === 'string' && text.toLowerCase().includes(searchKey)));
};

const filterItems = (items: DropmenuItemOptions[], searchKeys: string[], count = 0): [items: DropmenuItemOptions[], count: number] => {
    items = items.reduce<DropmenuItemOptions[]>((list, item) => {
        const {items: subItems} = item;
        if (subItems) {
            if (subItems.length) {
                const [subList, subCount] = filterItems(subItems, searchKeys);
                if (subList.length) {
                    list.push({...item, items: subList});
                    count += subCount;
                }
            }
        } else if (isItemMatch(item, searchKeys)) {
            list.push(item);
            count++;
        }
        return list;
    }, []);
    return [items, count];
};

export class DropmenuPop extends PickPop<DropmenuState, DropmenuPopProps, DropmenuPopState> {
    #searchbox: RefObject<SearchBox> = createRef();

    #list: RefObject<HTMLDivElement> = createRef();

    constructor(props: DropmenuPopProps) {
        super(props);
        this.state = {search: '', data: props.data, expanded: false, loading: !!props.fetcher && !props.data};
    }

    async load() {
        let {fetcher} = this.props;
        if (!fetcher) {
            return this.#scrollToActive();
        }
        if (typeof fetcher === 'string') {
            fetcher = {url: fetcher};
        }
        if (typeof fetcher === 'object') {
            const {url, ...fetchOptions} = fetcher as DropmenuFetchOptions;
            fetcher = async () => {
                const res = await fetch(url, {
                    headers: {'X-Requested-With': 'XMLHttpRequest'},
                    ...fetchOptions,
                });
                const data = await res.json();
                return data;
            };
        }

        this.setState({loading: true});
        let data = await fetcher();
        if ((data as unknown as {result: string, data: DropmenuData}).result) {
            data = (data as unknown as {result: string, data: DropmenuData}).data;
        }
        data = {...this.props.data, ...data};
        this.setState({data, loading: false}, this.#scrollToActive);
        this.props.onCacheData?.(data);
    }

    componentDidMount(): void {
        super.componentDidMount();
        if (!this.props.data) {
            this.load();
        }
        this.#searchbox.current?.focus();
    }

    #handleSearch = (value: string) => {
        this.setState({search: value});
    };

    #handleExpandBtnClick = () => {
        this.expand();
    };

    #handleItemClick = ({item, event}: {item: DropmenuItemOptions, event: MouseEvent}) => {
        if ((event.target as HTMLElement).closest('.is-link')) {
            this.props.togglePop(false, {
                text: item.text ?? `${item.type}:${item.id}`,
            });
            this.props.changeState({text: item.text ?? `${item.type || ''}:${item.id}`, value: item.id});
        }
    };

    #scrollToActive = () => {
        requestAnimationFrame(() => {
            const list = this.#list.current;
            if (!list) {
                return;
            }
            $(list).find('.dropmenu-item.active').scrollIntoView();
        });
    };

    activeTab = (name: string) => {
        this.setState({active: name});
    };

    expand = (expand?: boolean) => {
        this.setState({expanded: expand ?? !this.state.expanded});
    };

    protected _getTreeProps(tab: DropmenuTabData): DropmenuTreeProps {
        const {data: {labelMap, link, typeIconMap, typeLabelMap, itemProps, tree, itemType, checkIcon, hideDirIcon = true} = {}, search} = this.state;

        return {
            items: tab.items,
            labelMap,
            typeIconMap: {
                execution: 'run', project: 'project', product: 'product', program: 'cards-view', ...typeIconMap,
            },
            hideDirIcon,
            typeLabelMap,
            itemProps,
            itemType,
            checkIcon,
            onClickItem: this.#handleItemClick,
            tree,
            link,
            active: this.props.state.value,
            searchKeys: $.unique(search.toLowerCase().split(' ').filter(x => x.length)) as string[],
        };
    }

    protected _getData(): {tabs: DropmenuTabData[], activeTab: DropmenuTabData, expandData?: DropmenuTabData} {
        const {data: userData = {}, search, active} = this.state;
        const {tabs: tabList = [{name: 'other'}], expandName} = userData;
        let {data = []} = userData;
        if (Array.isArray(data)) {
            data = {other: data};
        }
        const dataMap: Record<string, DropmenuTabData> = {};
        const searchKeys = $.unique(search.toLowerCase().split(' ').filter(x => x.length)) as string[];
        let expandData: DropmenuTabData | undefined;
        let activeTabData: DropmenuTabData | undefined;
        Object.keys(data).forEach(name => {
            const isExpandTab = expandName === name;
            const tab = tabList.find(x => x.name === name) || {name};
            const items = (data as Record<string, DropmenuItemOptions[]>)[name] || [];
            const isActived = active === name && !isExpandTab;
            const [treeItems, count] = filterItems(items, searchKeys);
            const tabData = {
                name,
                items: treeItems,
                text: tab.text,
                active: isActived,
                count,
            };
            if (isExpandTab) {
                expandData = tabData;
            } else {
                dataMap[name] = tabData;
                if (isActived) {
                    activeTabData = tabData;
                }
            }
        });
        const tabs = tabList.reduce<DropmenuTabData[]>((list, {name}) => {
            if (name !== expandName) {
                list.push(dataMap[name] || {name, items: []});
            }
            return list;
        }, []);
        if (!activeTabData && tabs.length) {
            activeTabData = tabs[0];
        }
        return {tabs, expandData, activeTab: activeTabData!};
    }

    protected _renderList(tabs: DropmenuTabData[], activeTab: DropmenuTabData, expandData?: DropmenuTabData): ComponentChildren {
        const active = activeTab.name;
        const {expanded, data = {}} = this.state;
        let {hideSingleTab = true} = data;
        hideSingleTab = tabs.length === 1 && hideSingleTab;
        return (
            <Fragment>
                {hideSingleTab ? null : (
                    <div className="dropmenu-nav">
                        <ul key="nav" className="nav nav-secondary">
                            {tabs.map(({name, text, count}) => (
                                <li className="nav-item">
                                    <a className={`${active === name ? ' active' : ''}`} onClick={this.activeTab.bind(this, name)}>
                                        {text || name}
                                        {count ? <span className="label lighter rounded-full size-sm font-normal">{count}</span> : null}
                                    </a>
                                </li>
                            ))}
                        </ul>
                    </div>
                )}
                <div key="tab" className="flex-auto dropmenu-list scrollbar-hover scrollbar-thin">
                    <DropmenuTree
                        {...this._getTreeProps(activeTab)}
                    />
                </div>
                {expandData ? (
                    <div key="foot" className="dropmenu-foot flex-none toolbar justify-end border-t">
                        <Button type="ghost text-dark rounded gap-0.5 px-1.5" trailingIcon={`angle-${this.state.expanded ? 'left' : 'right'} opacity-60`} onClick={this.#handleExpandBtnClick}>{expandData.text || expandData.name} {(expandData.count && expanded) ? <span className="ml-1 label lighter rounded-full size-sm font-normal">{expandData.count}</span> : null}</Button>
                    </div>
                ) : null}
            </Fragment>
        );
    }

    protected _renderExpand(expandData: DropmenuTabData): ComponentChildren {
        return (
            <div className="col w-1/2 dropmenu-list scrollbar-thin scrollbar-hover">
                {<DropmenuTree
                    {...this._getTreeProps(expandData)}
                />}
            </div>
        );
    }

    protected _renderPop() {
        const {expanded, data = {}} = this.state;
        const {searchHint, search = true, title} = data;
        const {expandData, tabs, activeTab} = this._getData();
        const isExpanded = expanded && expandData;
        return (
            <Fragment>
                {search ? <div key="search" className="p-3 flex-none">
                    <SearchBox
                        ref={this.#searchbox}
                        className="size-md"
                        placeholder={searchHint}
                        onChange={this.#handleSearch}
                    />
                </div> : null}
                {title ? <div className="dropmenu-title">{title}</div> : null}
                <div class="row flex-auto min-h-0" ref={this.#list}>
                    <div class={`col w-${isExpanded ? '1/2' : 'full'}`}>
                        {this._renderList(tabs, activeTab, expandData)}
                    </div>
                    {isExpanded ? <div class="w-px bg-gray opacity-10" /> : null}
                    {expanded && expandData && this._renderExpand(expandData)}
                </div>
            </Fragment>
        );
    }

    protected _getClass(props: DropmenuPopProps) {
        const {expanded, loading, data = {}} = this.state;
        return classes('dropmenu load-indicator col', super._getClass(props), expanded && 'is-expanded', loading && 'loading', data.search !== false && 'has-search', data.title ? 'has-title' : '');
    }

    protected _getProps(props: DropmenuPopProps) {
        const {width = 248} = props;
        const {style, ...popProps} = super._getProps(props);
        const {expanded} = this.state;
        const trigger = this.trigger;
        const triggerBounding = trigger?.getBoundingClientRect();
        return {
            ...popProps,
            style: {
                ...(style as JSX.CSSProperties),
                width: width * (expanded ? 2 : 1),
                maxHeight: triggerBounding ? Math.max(triggerBounding.top - 8, window.innerHeight - triggerBounding.bottom - 8) : (style as JSX.CSSProperties).maxHeight,
            },
        };
    }
}
