import {JSX} from 'preact';
import {formatBytes} from '@zui/helpers/src/format-string';
import {ZinPerf} from '../types';
import {printData} from '../utils/console';

function getTimeClass(time: number, danger = 400, warning = 100) {
    if (time < warning) {
        return 'success';
    }
    if (time < danger) {
        return 'warning';
    }
    return 'danger';
}

function formatTime(time: number) {
    if (time < 1000) {
        return `${time.toFixed(0)}ms`;
    }
    return `${(time / 1000).toFixed(2)}s`;
}

export function ZinbarPerfBtn({perf}: {perf: ZinPerf}) {
    const name = perf.id === 'page' ? 'PAGE' : (perf.id === '#dtable' ? 'TABLE' : 'PART');
    const parts: JSX.Element[] = [];
    const {trace, xhprof} = perf;
    const request = trace?.request;
    if (perf.requestEnd) {
        const requestTime = perf.requestEnd - perf.requestBegin;
        parts.push(<div class={`px-0.5 state text-${getTimeClass(requestTime, 1000, 400)}`} data-hint={'Total load time (G<400<=N<1000<=B)'} onClick={printData.bind(null, 'Trace', 'Perf', perf, perf.id)}><i class="icon-history"></i> {formatTime(requestTime)}</div>);
        if (request) {
            const serverTime = request.timeUsed;
            parts.push(
                <div class="muted">/</div>,
                <div class={`px-0.5 state text-${getTimeClass(serverTime)}`} data-hint={'Server time (G<100<=N<400<=B)'} onClick={printData.bind(null, 'Trace', 'Request', request, perf.id)}><span class="scale-95 font-bold inline-block">S</span>{formatTime(serverTime)}</div>,
            );
        }
        if (perf.dataSize) {
            if (request) {
                const netTime = requestTime - request.timeUsed;
                parts.push(
                    <div class="muted">/</div>,
                    <div class={`px-0.5 state text-${getTimeClass(netTime, 600, 200)}`} data-hint={'Network time (G<200<=N<600<=B)'} onClick={printData.bind(null, 'Trace', 'Request', request, perf.id)}><span class="scale-95 font-bold inline-block">N</span>{formatTime(netTime)}</div>,
                );
            }
            parts.push(
                <div class="px-0.5 state" data-hint={'Loaded data size'} onClick={printData.bind(null, 'Trace', 'Perf', perf, perf.id)}><span class="muted"><i class="icon icon-cube muted"></i> {formatBytes(perf.dataSize, 1)}</span></div>,
            );
            if (request) {
                const netTime       = requestTime - request.timeUsed;
                const downloadSpeed = 1000 * perf.dataSize / netTime;
                parts.push(
                    <div class="muted">/</div>,
                    <div class={`px-0.5 state text-${downloadSpeed < 102400 ? 'danger' : (downloadSpeed < 1024000 ? 'warning' : 'success')}`} data-hint={'Download speed(B<100KB<=N<1MB<=G)'} onClick={printData.bind(null, 'Trace', 'Request', request, perf.id)}><i class="icon icon-arrow-down"></i>{formatBytes(downloadSpeed, 1)}/s</div>,
                );
            }
        }
        if (perf.renderEnd && perf.renderBegin) {
            const renderTime = perf.renderEnd - perf.renderBegin;
            const renderClass = getTimeClass(renderTime, 200, 50);
            parts.push(
                <div class="muted">/</div>,
                <div class={`px-0.5 state text-${renderClass}`} data-hint={'Client render time (G<50<=N<200<=B)'} onClick={printData.bind(null, 'Trace', 'Perf', perf, perf.id)}><i class={`icon-${renderClass === 'danger' ? 'frown' : (renderClass === 'warning' ? 'meh' : 'smile')}`}></i>{formatTime(renderTime)}</div>,
            );
        }
        if (request) {
            const {memory, querys} = request;
            if (typeof memory === 'number') {
                parts.push(
                    <div class="muted">/</div>,
                    <div class={`px-0.5 state text-${getTimeClass(memory, 1024000, 102400)}`} data-hint={'Server memory usage(G<10KB<=N<100KB<=B)'} onClick={printData.bind(null, 'Trace', 'Request', request, perf.id)}><span class="scale-95 font-bold inline-block">M</span>{formatBytes(memory)}</div>,
                );
            }
            if (typeof querys === 'number') {
                parts.push(
                    <div class="muted">/</div>,
                    <div class={`px-0.5 state text-${getTimeClass(querys, 30, 10)}`} data-hint={'Server sql queries count (G<30<=N<10<=B)'} onClick={printData.bind(null, 'SQL Query', `${trace.sqlQuery?.length ?? 0} queries`, trace.sqlQuery, perf.id)}><span class="scale-95 font-bold inline-block">Q</span>{querys}</div>,
                );
            }
        }
        if (trace?.files) {
            parts.push(
                <div class="muted">/</div>,
                <div class={'px-0.5 state'} data-hint={'Server loaded php files count'} onClick={printData.bind(null, 'Trace', `${trace.files.length} php files`, trace.files, perf.id)}><span class="muted"><i class="icon-file icon-sm muted scale-75"></i>{trace.files.length}</span></div>,
            );
        }
        const profiles = trace?.profiles;
        if (profiles?.length) {
            let slowSqlCount = 0;
            let maxSql = {Duration: 0};
            profiles.forEach((item) => {
                if (item.Duration > 0.3) {
                    slowSqlCount++;
                }
                if (item.Duration > maxSql.Duration) {
                    maxSql = item;
                }
            });
            parts.push(
                <div class={`px-0.5 state text-${getTimeClass(slowSqlCount, 3, 1)}`} data-hint={'Server slow SQL queries count (G<3<=N<1<=B)'} onClick={printData.bind(null, 'SQL Query', `${profiles.length} SQL profiles`, profiles, perf.id)}><span class="scale-95 font-bold inline-block">LQ</span>{slowSqlCount}</div>,
            );
            if (maxSql.Duration) {
                const maxDuration = maxSql.Duration * 1000;
                parts.push(
                    <div class={`px-0.5 state text-${getTimeClass(maxDuration, 600, 300)}`} data-hint={'Server lowest SQL query duration (G<600<=N<300<=B)'} onClick={printData.bind(null, 'SQL Query', `Slowest SQL query: ${maxDuration}ms`, maxSql, perf.id)}><span class="scale-95 font-bold inline-block">MLQ</span>{formatTime(maxDuration)}</div>,
                );
            }
        }
    } else {
        parts.push(<div class="muted px-0.5">loading...</div>);
    }
    return (
        <div class="zin-perf-btn-list row items-center bg-black text-sm">
            <div class="px-1 bg-canvas bg-opacity-20 self-stretch flex items-center" data-hint={`REQUEST: ${perf.id} URL: ${perf.url}`}><span class="muted">{name}</span></div>
            {parts}
            {xhprof ? <a class="state text-secondary px-0.5" href={xhprof} target="_blank" data-hint="Visit xhprof page">XHP</a> : null}
        </div>
    );
}
