import {Modal, ModalConfirmOptions} from '@zui/modal';

Object.assign(Modal, {
    reconfirm(options: ModalConfirmOptions) {
        return Modal.confirm(Object.assign({
            iconClass: 'warning rounded-full center w-8 h-8 flex-none',
            type: 'custom',
            custom:  {
                className: 'modal-reconfirm',
                closeBtn: false,
            },
        }, options));
    },
});
