import {ComponentType, JSX} from 'preact';
import {CellInfo, DTablePlugin, DTableWithPlugin} from '@zui/dtable/src/types';
import {$} from '@zui/core';
import {custom, DTableCustomHTMLElementType, DTableCustomHTMLCode, DTableCustomTypes} from '@zui/dtable/src/plugins/custom';
import {definePlugin} from '@zui/dtable/src/helpers/shared-plugins';
import {Picker} from '@zui/picker/src/component';
import {DatePicker, TimePicker} from '@zui/datetime-picker/src/component';
import {PriPicker, SeverityPicker} from '@zentao/picker/src/components';
import './style.css';

export type DTableFormControlInfo = {
    type: string;
    props?: Record<string, unknown> | ((this: DTableForm, cell: CellInfo) => Record<string, unknown>);
    component?: DTableCustomHTMLCode | DTableCustomHTMLElementType | ComponentType;
};

export type DTableFormControlSetting = string | DTableFormControlInfo | ((this: DTableForm, cell: CellInfo) => DTableFormControlInfo);

export type DTableFormTypes = {
    col: {
        control?: DTableFormControlSetting;
    },
    data: {
        formData: Record<string, unknown>;
    },
    options: {
        controlMap?: Record<string, DTableFormControlInfo>;
        onFormChange?: (this: DTableForm, name: string, value: unknown, formData: Record<string, unknown>) => void;
    },
    methods: {
        setFormData(this: DTableForm, name: string, value: unknown): void;
        getFormData(this: DTableForm, name?: string): unknown;
    }
};

export type DTableForm = DTableWithPlugin<DTableFormTypes, [DTableCustomTypes]>;

const getInputProps = (table: DTableForm, cell: CellInfo, props?: Record<string, unknown> | null, valueProp = 'value') => {
    const name = `${cell.col.name}[${cell.row.id}]`;
    const value = table.getFormData(name) ?? cell.value;
    return {
        [valueProp]: value,
        className: 'form-control',
        onChange: (e: Event | string) => table.setFormData(name, e instanceof Event ? (e.currentTarget as HTMLInputElement).value : e),
        ...props,
    };
};

function getPickProps(this: DTableForm, cell: CellInfo) {
    return getInputProps(this, cell, null, 'defaultValue');
}

const defaultControlMap: Record<string, Omit<DTableFormControlInfo, 'type'>> = {
    input: {
        component: 'input',
        props(cell) {
            return getInputProps(this as DTableForm, cell, {type: 'text'});
        },
    },
    select: {
        component: function (props: {items: {text: string, value: string}[]} & JSX.HTMLAttributes<HTMLSelectElement>) {
            const {items, defaultValue, ...others} = props;
            return (
                <select {...others}>
                    {items.map((item) => (
                        <option key={item.value} value={item.value}>{item.text}</option>
                    ))}
                </select>
            );
        } as ComponentType,
        props(cell) {
            return getInputProps(this, cell);
        },
    },
    picker: {
        component: Picker as unknown as ComponentType,
        props: getPickProps,
    },
    datePicker: {
        component: DatePicker as unknown as ComponentType,
        props(cell) {
            return getInputProps(this, cell, {className: 'flex-auto', icon: 'calendar'}, 'defaultValue');
        },
    },
    timePicker: {
        component: TimePicker as unknown as ComponentType,
        props(cell) {
            return getInputProps(this, cell, {className: 'flex-auto', icon: 'time'}, 'defaultValue');
        },
    },
    priPicker: {
        component: PriPicker as unknown as ComponentType,
        props: getPickProps,
    },
    severityPicker: {
        component: SeverityPicker as unknown as ComponentType,
        props: getPickProps,
    },
};

const formPlugin: DTablePlugin<DTableFormTypes, [DTableCustomTypes]> = {
    name: 'form',
    plugins: [custom],
    data() {
        return {formData: {}};
    },
    colTypes: {
        control: {
            custom(cell: CellInfo) {
                let {control} = cell.col.setting as DTableFormTypes['col'];
                if (typeof control === 'function') {
                    control = control.call(this as DTableForm, cell);
                }
                if (!control) {
                    return;
                }
                if (typeof control === 'string') {
                    control = {type: control};
                }
                const {controlMap} = this.options as unknown as DTableFormTypes['options'];
                const {type} = control;
                const props = {name: `${cell.col.name}[${cell.row.id}]`};
                let component: DTableFormControlInfo['component'];
                [defaultControlMap[type], controlMap?.[type], control].forEach((current) => {
                    if (current) {
                        const {props: currentProps} = current;
                        if (currentProps) {
                            $.extend(props, typeof currentProps === 'function' ? currentProps.call(this as DTableForm, cell) : currentProps);
                        }
                        component = current.component || component;
                    }
                });

                if (!component) {
                    return;
                }

                return {
                    component,
                    props: props,
                };
            },
        },
    },
    methods: {
        setFormData(name, value) {
            const {formData} = this.data;
            formData[name] = value;
            this.options.onFormChange?.call(this, name, value, formData);
        },
        getFormData(name) {
            if (name) {
                return this.data.formData[name];
            }
            return this.data.formData;
        },
    },
};

export const form = definePlugin(formPlugin);
