import {ContainerNode, render} from 'preact';
import {Component, $} from '@zui/core';
import {Popovers} from '@zui/popovers';
import {GlobalSearchPanel} from '../component/global-search-panel';
import {GlobalSearchProps} from '../types';

export class GlobalSearch extends Component<GlobalSearchProps, {}, HTMLElement> {
    static NAME = 'GlobalSearch';

    init() {
        const {panelID = 'global-search-panel', searchFunc, commonSearchKey} = this.options;
        const $panel = $(`<div id="${panelID}" />`).css({width: 270});
        this.$element.after($panel);
        const $input = this.$element.find('input');
        const $btn = this.$element.find('button');
        const delay = 500;
        $input
            .data('target', `#${panelID}`)
            .on('input', (e) => {
                render(
                    <GlobalSearchPanel
                        {...this.options}
                        searchValue={e.target.value}
                        selectedKey={$input.data('selectedKey')}
                    />,
                    $panel[0] as ContainerNode,
                );
            })
            .on('keydown', (e) => {
                if (e.code === 'Enter') {
                    searchFunc(commonSearchKey, e.target.value);
                    $input.trigger('blur');
                }
            })
            .on('blur', () => {
                $input.data('lastValue', $input.val());
                setTimeout(() => {
                    $input.val('');
                    $input.trigger('input');
                }, delay);
                setTimeout(() => {
                    $input.data('lastValue', '');
                }, 1000);
            });
        $btn.on('click', () => {
            searchFunc(commonSearchKey, $input.data('lastValue') as string);
        });
        new Popovers(this.$element.find('input'), {
            placement: 'top-start',
            trigger: 'focus blur',
            offset: 4,
            mask: false,
            delay: delay,
        });
    }
}
