import {SeverityLabel} from '@zentao/labels/src/components/severity-label';
import {PickOptions} from '@zui/pick/src/types';
import {SeverityPickerOptions} from '../types';
import {PriPicker} from './pri-picker';

export class SeverityPicker extends PriPicker<SeverityPickerOptions> {
    static defaultProps = {
        ...PriPicker.defaultProps,
        items: [0, 1, 2, 3, 4],
    } as Partial<PickOptions>;

    _renderItem(value?: string, text?: string, key?: string) {
        return <SeverityLabel key={key} severity={value} text={text} />;
    }
}
