import {RenderableProps} from 'preact';
import {Pick} from '@zui/pick/src/components';
import {DropmenuBtnProps, DropmenuPopProps, DropmenuOptions, DropmenuState, DropmenuData} from '../types';
import {DropmenuPop} from './dropmenu-pop';
import {DropmenuBtn} from './dropmenu-btn';
import {classes} from '@zui/core/src/helpers';

export class Dropmenu extends Pick<DropmenuState, DropmenuOptions> {
    static defaultProps: Partial<DropmenuOptions> = {
        ...Pick.defaultProps,
        popWidth: 248,
        maxWidth: 160,
        className: 'dropmenu-btn btn ghost',
        tagName: 'button',
        clickType: 'toggle',
        leadingAngle: true,
        caret: true,
        cache: 60 * 1000 * 4,
    };

    static Pop = DropmenuPop as typeof Pick.Pop;

    static Trigger = DropmenuBtn as typeof Pick.Trigger;

    #cacheData?: DropmenuData;

    #cacheTimer?: number;

    #setCacheData = (data?: DropmenuData) => {
        const {cache} = this.props;
        if (!cache) {
            return;
        }
        this.#cacheData = data;
        if (typeof cache === 'number') {
            if (this.#cacheTimer) {
                clearTimeout(this.#cacheTimer);
            }
            this.#cacheTimer = window.setTimeout(() => {
                this.#cacheData = undefined;
                this.#cacheTimer = 0;
            }, cache);
        }
    };

    componentWillUnmount(): void {
        super.componentWillUnmount();
        if (this.#cacheTimer) {
            clearTimeout(this.#cacheTimer);
        }
    }

    componentWillReceiveProps(nextProps: Readonly<DropmenuOptions>): void {
        if (this.props.data !== nextProps.data || this.props.fetcher !== nextProps.fetcher) {
            this.#cacheData = undefined;
        }
    }

    protected _getTriggerProps(props: RenderableProps<DropmenuOptions>, state: Readonly<DropmenuState>): DropmenuBtnProps {
        const {className, ...others} = super._getTriggerProps(props, state);
        const {value = ''} = state;
        return {
            ...others,
            className: classes(className, {'has-value': value.length}),
            text: props.text,
            caret: props.caret,
            maxWidth: props.maxWidth,
            leadingAngle: props.leadingAngle,
        } as DropmenuBtnProps;
    }

    protected _getPopProps(props: RenderableProps<DropmenuOptions>, state: Readonly<DropmenuState>): DropmenuPopProps {
        return {
            ...(super._getPopProps(props, state) as DropmenuPopProps),
            data: this.#cacheData || props.data,
            fetcher: props.fetcher,
            cache: !!props.cache,
            onCacheData: this.#setCacheData,
        };
    }
}
