import {ZentaoLink} from './types';

export function parseUrlParams(params?: string): Record<string, string> {
    params = params || location.search;
    if (params[0] === '?') {
        params = params.substring(1);
    }
    try {
        const searchParams = new URLSearchParams(params);
        const paramsObject: Record<string, string> = {};
        for (const key of searchParams.keys()) {
            paramsObject[key] = searchParams.getAll(key).join(',');
        }
        return paramsObject;
    } catch (_) {
        return {};
    }
}

export function parseLink(url?: string): ZentaoLink {
    if (!url) {
        return {url};
    }

    const {config} = window;
    if (/^https?:\/\//.test(url)) {
        const origin = window.location.origin;
        if (!url.includes(origin)) {
            return {external: true, url: url};
        }
        url = url.substring((origin + config.webRoot).length);
    }
    const hashParts = url.split('#');
    const urlParts = hashParts[0].split('?');
    const search = urlParts[1];
    const params = search ? parseUrlParams(search) : {};
    let path = urlParts[0];
    const link: ZentaoLink = {
        url: url,
        isOnlyBody: params.onlybody === 'yes',
        vars: [],
        hash: hashParts[1] || '',
        params: params,
        tid: (params.tid as string) || '',
    }; // parse moduleName, methodName, vars, viewType, isOnlyBody, prj
    if (config.requestType === 'GET') {
        link.moduleName = (params[config.moduleVar] as string) || 'index';
        link.methodName = (params[config.methodVar] as string) || 'index';
        link.viewType = (params[config.viewVar] as string) || config.defaultView;
        for (const name in params) {
            if (name !== config.moduleVar && name !== config.methodVar && name !== config.viewVar && name !== 'onlybody' && name !== 'tid') {
                link.vars!.push([name, params[name]]);
            }
        }
    } else {
        let slashIndex = path.lastIndexOf('/');
        if (slashIndex === path.length - 1) {
            path = path.substring(0, slashIndex);
            slashIndex = path.lastIndexOf('/');
        }
        if (slashIndex >= 0) {
            path = path.substring(slashIndex + 1);
        }
        const dotIndex = path.lastIndexOf('.');
        if (dotIndex >= 0) {
            link.viewType = path.substring(dotIndex + 1);
            path = path.substring(0, dotIndex);
        } else {
            link.viewType = config.defaultView;
        }
        const pathParts = path.split(config.requestFix);
        link.moduleName = pathParts[0] || 'index';
        link.methodName = pathParts[1] || 'index';
        if (pathParts.length > 2) {
            for (let i = 2; i < pathParts.length; i++) {
                link.vars!.push(['', pathParts[i]]);
                params['$' + (i - 1)] = pathParts[i];
            }
        }
    }
    return link;
}

export function createLink(moduleName: string | {moduleName: string, methodName: string, vars?: string, viewType?: string, hash?: string, params?: Record<string, unknown>}, methodName: string, vars?: string | string[] | [string, string][], viewType?: string, hash?: string, params?: Record<string, unknown>): string {
    if (typeof moduleName === 'object') {
        return createLink(moduleName.moduleName, moduleName.methodName, moduleName.vars, moduleName.viewType, moduleName.hash, moduleName.params);
    }

    const config = window.config;
    if (!viewType) {
        viewType = config.defaultView;
    }
    if (vars) {
        if (typeof vars === 'string') {
            vars = vars.split('&');
        }
        for (let i = 0; i < vars.length; i++) {
            const varItem = vars[i];
            if (typeof varItem === 'string') {
                const parts = varItem.split('=');
                vars[i] = [parts.shift() as string, parts.join('=')];
            }
        }
    }

    const linkParts: string[] = [];
    const isGetRequest = config.requestType === 'GET';
    if (isGetRequest) {
        linkParts.push(config.router, '?', config.moduleVar, '=', moduleName, '&', config.methodVar, '=', methodName);
        if (vars) {
            for (let i = 0; i < vars.length; i++) {
                linkParts.push('&', vars[i][0], '=', vars[i][1]);
            }
        }
        linkParts.push('&', config.viewVar, '=', viewType);
    } else {
        if (config.requestType == 'PATH_INFO') {
            linkParts.push(config.webRoot, moduleName, config.requestFix, methodName);
        }
        if (config.requestType == 'PATH_INFO2') {
            linkParts.push(config.webRoot, 'index.php/', moduleName, config.requestFix, methodName);
        }
        if (vars) {
            for (let i = 0; i < vars.length; i++) {
                linkParts.push(config.requestFix + vars[i][1]);
            }
        }
        linkParts.push('.', viewType);
    }

    if (params) {
        Object.keys(params).forEach((name) => {
            const value = params[name];
            if (name[0] === '$') {
                return;
            }
            linkParts.push((!isGetRequest && !linkParts.includes('?')) ? '?' : '&', name, '=', value as string);
        });
    }

    if (typeof hash === 'string') {
        linkParts.push(hash.startsWith('#') ? '' : '#', hash);
    }
    return linkParts.join('');
}
